import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Schema} from '../schema';
import { TreeNode } from '@farris/ui-treetable';
import { CurrentNode } from '../currentNode';
@Component({
  selector: 'app-expression-schema-grid',
  templateUrl: './expression-schema-grid.component.html',
  styleUrls: ['./expression-schema-grid.component.css']
})
export class ExpressionSchemaGridComponent implements OnInit {
  @Input() schema = new Map<string, Schema>();
  @Output() selectschemachange = new EventEmitter();
  currentNode:CurrentNode=new CurrentNode;
  cols = [
    { field: 'name', title: 'çº²è¦åç§°'}
  ];
  public treedata: TreeNode[] = [];
  constructor() { }
  ngOnInit() {
    this.convertSchema();
  }
  selectRowChange($event)
  {
    this.currentNode = new CurrentNode;
    this.currentNode.id = $event.node.data.id;
    this.currentNode.name = $event.node.data.name;
    this.currentNode.parentName = $event.node.parent.data.name;
    this.selectschemachange.emit(this.currentNode);
  }
  convertSchema(): void {
    this.schema.forEach((value, key) => {
      let treeitem:TreeNode={
        data:
        {
          "id":Math.random(),
          "name": "",
          "type": "",
          "description": ""
        },
        children: []
      };
      treeitem.data.name = key;
      treeitem.data.type = "Schema";
      treeitem.data.description = value.description;
      value.entityTypes.forEach(entitytype => {
        let typetree:TreeNode={
          data:
          {
            "id":"",
            "name": "",
            "type": "",
            "description": ""
          },
          children: [],
          parent:treeitem
        };
        typetree.data.id = entitytype.id;
        typetree.data.name = entitytype.name;
        typetree.data.type = "EntityType";
        typetree.data.description = entitytype.description;
        treeitem.children.push(typetree);
      });
      this.treedata.push(treeitem);
    });
  }

}
