  insert(index: number) {
      const _newItem = Object.assign({}, this.newItem);
      this.conditions.splice(index+1, 0, _newItem);
      // if (index  === 0) {
      //     this.conditions.unshift(_newItem);
      // } else {
      //     this.conditions.splice(index+1, 0, _newItem);
      // }

      this.changeConditionList();
  }

  remove(index: number) {
    let num = index+1;
    this.msgSer.confirm('ç¡®è®¤å é¤ç¬¬'+num+'è¡æ°æ®?').subscribe((result) => {
      if(result){
        if(index!=0)
        {
          this.conditions.splice(index, 1);
        }
        else if(this.conditions.length>1)
        {
          this.conditions.splice(index,1);
        }
        else
        {
          this.conditions=[
            {
              computeField: '',
              fieldName:'',
              value: '',
              lbracket: '(',
              rbracket: ')',
              relation: "&&",
              operator: "=="
            }
          ]
        }
        this.changeConditionList();
        if (this.currentCondition) {
          if (index === this.currentCondition.index) {
              this.currentCondition = null;
          }
        }
      }
    });   
  }

