  /***è½¬åä¸ºè¡¨è¾¾å¼ä¸² */
  private convertExpr(conditions:ConditionRule[]):string
  {
    this.expr="";
    if(conditions)
    {
      let count=0;
      let operator="";
      let length=conditions.length;
      conditions.forEach(item=>{
        count++;
        if(count<length)
        {
          if(item.operator=="==" || item.operator=="!=" || item.operator==">" ||
          item.operator==">=" || item.operator=="<" || item.operator=="<=")
          {
            this.expr=this.expr+item.lbracket+item.computeField + item.operator +item.value+item.rbracket+item.relation;
          }
          else
          {
            switch(item.operator)
            {
              case "equal":this.expr=this.expr+item.lbracket+item.computeField +"==\"" + item.value +"\""+item.rbracket+item.relation;break;
              case "notequal":this.expr=this.expr+item.lbracket+item.computeField +"!=\"" + item.value +"\""+item.rbracket+item.relation;break;
              case "==null":this.expr=this.expr+item.lbracket+item.computeField +"==null"+item.rbracket+item.relation;break;
              case "!=null":this.expr=this.expr+item.lbracket+item.computeField +"!=null"+item.rbracket+item.relation;break;
              case "Contains":this.expr=this.expr+item.lbracket+"DefaultFunction.Contains("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
              case "NotContains":this.expr=this.expr+item.lbracket+"DefaultFunction.NotContains("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
              case "StartsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.StartsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
              case "NotStartsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.NotStartsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
              case "EndsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.EndsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
              case "NotEndsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.NotEndsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket+item.relation;break;
            }
          }
        }
        else
        {
          if(item.operator=="==" || item.operator=="!=" || item.operator==">" ||
          item.operator==">=" || item.operator=="<" || item.operator=="<=")
          {
            this.expr=this.expr+item.lbracket+item.computeField + item.operator +item.value+item.rbracket;
          }
          else
          {
            switch(item.operator)
            {
              case "equal":this.expr=this.expr+item.lbracket+item.computeField +"==\"" + item.value +"\""+item.rbracket;break;
              case "notequal":this.expr=this.expr+item.lbracket+item.computeField +"!=\"" + item.value +"\""+item.rbracket;break;
              case "==null":this.expr=this.expr+item.lbracket+item.computeField +"==null"+item.rbracket;break;
              case "!=null":this.expr=this.expr+item.lbracket+item.computeField +"!=null"+item.rbracket;break;
              case "Contains":this.expr=this.expr+item.lbracket+"DefaultFunction.Contains("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
              case "NotContains":this.expr=this.expr+item.lbracket+"DefaultFunction.NotContains("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
              case "StartsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.StartsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
              case "NotStartsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.NotStartsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
              case "EndsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.EndsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
              case "NotEndsWith":this.expr=this.expr+item.lbracket+"DefaultFunction.NotEndsWith("+item.computeField+",\""+item.value+"\")"+item.rbracket;break;
            }
          }
        }
      })
    }
    return this.expr;
  }
 
  //åå§åSchemaæ ¼å¼çEntity
  OninitSchemaEntity(injectedEntities:InjectedEntity[],schema:Map<string,Schema>) {
    if(injectedEntities && injectedEntities.length>0)
    {
      this.entities = [];
    //å®ä½æ¾ç¤ºç¦å¿è¡¨
    let navigatorMap = new Map<string, InjectedType>();
    let numcount = 1;
    //è§£ææ¯ä¸ä¸ªæ³¨å¥å®ä½ï¼å°å¶è½¬åä¸ºTreeNode
    injectedEntities.forEach(injectEntity => {
      
      //æ ¹æ®schemaåç§°æ¾å°å¯¹åºçschema
      let currentSchema = schema.get(injectEntity.schemaName);
      switch (injectEntity.injectedType) {
        //æ³¨å¥çè¥ä¸ºå®ä½é
        case InjectedType.EntitySet: {
          // //æ ¹æ®æ³¨å¥å®ä½çåç§°æ¾å°å¯¹åºçå®ä½é
          //let entitySet = currentSchema.entitySets.find(entitySet => entitySet.name == injectEntity.injectedName);
          // //å°æ¾å°çå®ä½éè½¬åä¸ºTreeNode
          // entityTree = this.ConvertEntitySet(entitySet, currentSchema, navigatorMap);
        }; break;
        //æ³¨å¥çè¥ä¸ºå®ä½ç±»å
        case InjectedType.EntityType: {
          let entityType = currentSchema.entityTypes.find(entityType => entityType.name == injectEntity.injectedName);
          this.ConvertEntityType(entityType,"",currentSchema,navigatorMap,1,"",numcount);
        }; break;
      }
      numcount++;
    });
    }
    
  }
