import { Component, OnInit, OnChanges, Input, Output, EventEmitter, ViewChild, TemplateRef } from '@angular/core';
import { Schema, NavigationType, EntityType, EntitySet } from '../schema';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';
import { CurrentNode } from '../currentNode';
import { InjectedEntity } from '../injected-entity';
import { InjectedType } from '../injected-type';
import { ExpressionEditorLocalePipe } from '../expression-editor-locale.pipe';
import { NewEntityType, NewNavigationType, NewSchema, NewEntitySet } from '../i18n/NewSchema';
import _ from 'lodash';

@Component({
  selector: 'app-expression-entity-grid',
  templateUrl: './expression-entity-grid.component.html',
  styleUrls: ['./expression-entity-grid.component.css']
})
export class ExpressionEntityGridComponent implements OnInit, OnChanges {

  //å®ä¹i18næ è¯
  private localeId: string;
  private localePipe: ExpressionEditorLocalePipe;

  currentNode: CurrentNode = new CurrentNode;
  entities: TreeNode[] = [];
  currentSchemaName: string = "";
  currentNewSchemaName: string = "";
  currentParent: string = "";
  @Input() schema = new Map<string, Schema>();
  @Input() newSchemaMap = new Map<string, NewSchema>();
  @Input() injectedEntities: InjectedEntity[];
  @Output() selectentitychange = new EventEmitter();
  @Output() dbclickInsert = new EventEmitter();

  @ViewChild('tt') tt: TreeTableComponent;

  cols = [];
  public treedata: TreeNode[] = [];

  // data: TreeNode[] = [];
  // temp2: TreeNode;
  // temp3: TreeNode;
  // all: TreeNode[] = [];

  constructor() {
    //åå§åi18næ è¯
    this.localeId = localStorage.getItem('languageCode');
    this.localePipe = new ExpressionEditorLocalePipe(this.localeId);
  }
  ngOnInit() {
    this.cols = [
      { field: 'name', title: this.localePipe.transform("EntityList") },
      { field: 'description', title: this.localePipe.transform("EntityName") },
      { field: 'type', title: this.localePipe.transform("EntityType"), width: '100' }
    ];
    if (this.newSchemaMap && this.newSchemaMap.size > 0) {
      this.onInitNewSchema();
      this.treedata = _.cloneDeep(this.entities);
    } else if (this.schema && this.schema.size > 0) {
      this.OninitSchemaEntity();
      this.treedata = _.cloneDeep(this.entities);
    }
  }
  selectRowChange($event) {
    this.currentNode = new CurrentNode;
    this.currentNode.id = $event.node.data.id;
    this.currentNode.name = $event.node.data.name;
    this.currentNode.parentName = this.selectParentString($event.node);
    this.currentParent = this.selectParentString($event.node);
    this.currentNode.schema = $event.node.data.schema;
    this.currentNode.nodeType = $event.node.data.nodeType;
    this.currentNode.description = $event.node.data.description;
    this.selectentitychange.emit(this.currentNode);
  }
  dblClick($event) {
    if (this.currentParent) {
      this.dbclickInsert.emit(this.currentParent + "." + $event.data.name);
    }
    else {
      this.dbclickInsert.emit($event.data.name);
    }
  }
  ngOnChanges(): void {
    if (this.newSchemaMap && this.newSchemaMap.size > 0) {
      this.onInitNewSchema();
      this.treedata = _.cloneDeep(this.entities);
      // this.treedata = this.entities;
      // this.treedata = [...this.treedata];
    } else if (this.schema && this.schema.size > 0) {
      this.OninitSchemaEntity();
      this.treedata = _.cloneDeep(this.entities);
      // this.treedata = this.entities;
      // this.treedata = [...this.treedata];
    }
  }
  //éå½ç¶èç¹å
  selectParentString(node: any) {
    if (node.parent) {
      if (this.selectParentString(node.parent)) {
        if (this.strContain(node.parent.data.type))
          return this.selectParentString(node.parent) + "." + node.parent.data.name + "[0]";
        else
          return this.selectParentString(node.parent) + "." + node.parent.data.name;
      }
      else if (this.strContain(node.parent.data.type)) {
        return node.parent.data.name + "[0]";
      }
      else {
        return node.parent.data.name;
      }
    }
    else {
      return "";
    }
  }


  //åå§åSchemaæ ¼å¼çEntity
  OninitSchemaEntity() {
    if (this.injectedEntities && this.injectedEntities.length > 0) {
      this.entities = [];
      let entityTree: TreeNode = {
        data:
        {
          "id": Math.random(),
          "name": "",
          "type": "",
          "description": "",
          "schema": "",
          "nodeType": ""
        },
        children: []
      };
      //å®ä½æ¾ç¤ºç¦å¿è¡¨
      let navigatorMap = new Map<string, InjectedType>();
      //è§£ææ¯ä¸ä¸ªæ³¨å¥å®ä½ï¼å°å¶è½¬åä¸ºTreeNode
      this.injectedEntities.forEach(injectEntity => {

        //æ ¹æ®schemaåç§°æ¾å°å¯¹åºçschema
        let currentSchema = this.schema.get(injectEntity.schemaName);
        this.currentSchemaName = injectEntity.schemaName;
        switch (injectEntity.injectedType) {
          //æ³¨å¥çè¥ä¸ºå®ä½é
          case InjectedType.EntitySet: {
            //æ ¹æ®æ³¨å¥å®ä½çåç§°æ¾å°å¯¹åºçå®ä½é
            let entitySet = currentSchema.entitySets.find(entitySet => entitySet.name == injectEntity.injectedName);
            //å°æ¾å°çå®ä½éè½¬åä¸ºTreeNode
            entityTree = this.ConvertEntitySet(entitySet, currentSchema, navigatorMap);
          }; break;
          //æ³¨å¥çè¥ä¸ºå®ä½ç±»å
          case InjectedType.EntityType: {
            let entityType = currentSchema.entityTypes.find(entityType => entityType.name == injectEntity.injectedName);
            //å°å®ä½ç±»ååç§°æ¹ä¸ºèªå®ä¹åç§°
            //entityType.name = injectEntity.name;
            //å°æ¾å°çå®ä½ç±»åè½¬åä¸ºTreeNode
            entityTree = this.ConvertEntityType(entityType, currentSchema, navigatorMap);
          }; break;
        }
        entityTree.data.name = injectEntity.name;
        //å°æ¯ä¸ªé¢ç½®å¯¹è±¡è½¬ååçTreeNode
        this.entities.push(entityTree);
      });
    }

  }
  //å®ä½éè½¬å
  ConvertEntitySet(entitySet: EntitySet, schema: Schema, navigatorMap: any): TreeNode {
    if (entitySet) {
      //å°entitysetè½¬åä¸ºTreeNodeç»æç¨äºå±ç¤º
      let treeNode: TreeNode = {
        data:
        {
          "id": "",
          "name": entitySet.name,
          "type": "EntitySet" + "[" + entitySet.entityType + "]",
          "description": entitySet.description,
          "schema": this.currentSchemaName,
          "nodeType": "Complex"
        },
        children: []
      };
      //å°è½¬åå®æçå®ä½éæ·»å è¿ç¦å¿è¡¨ï¼é²æ­¢å¾ªç¯è½¬å
      navigatorMap.set(entitySet.name, InjectedType.EntitySet);
      //æ¾å°å®ä½éå¯¹åºçå®ä½ç±»å
      let entityType = schema.entityTypes.find(entityType => entityType.name == entitySet.entityType);
      //å°entitytypeè½¬åä¸ºTreeNodeç»æç¨äºå±ç¤º
      this.ConvertEntityType(entityType, schema, navigatorMap).children.forEach(child => {
        treeNode.children.push(child);
      });
      return treeNode;
    }
    //å®ä½éä¸ºç©ºææªå®ä¹åè¿åç©º
    else return null;
  }
  //å®ä½ç±»åè½¬å
  ConvertEntityType(entityType: EntityType, schema: Schema, navigatorMap: any): TreeNode {
    if (entityType) {
      let treeNode: TreeNode = {
        data:
        {
          "id": entityType.id,
          "name": entityType.name,
          "type": "EntityType",
          "description": entityType.description,
          "schema": this.currentSchemaName,
          "nodeType": "Complex"
        },
        children: []
      };
      //å°è½¬åçentitytypeæ·»å è¿ç¦å¿è¡¨
      navigatorMap.set(entityType.name + entityType.id, InjectedType.EntityType);
      //å°æ¯ä¸ªå±æ§è½¬åä¸ºæ¯ä¸ªTreeNode
      entityType.properties.forEach(property => {
        let propertyTree: TreeNode = {
          data:
          {
            "id": Math.random(),
            "name": "",
            "type": property.type,
            "description": property.description,
            "schema": this.currentSchemaName,
            "nodeType": "Simple"
          },
          children: []
        };
        //æ ¹æ®æ¡ä»¶å¤æ­è¯¥å±æ§æ¯å¦ä¸ºä¸»é®
        if (property.name == entityType.key) {
          propertyTree.data.name = property.name + "*";
        }
        else {
          propertyTree.data.name = property.name;
        }
        propertyTree.parent = treeNode;
        treeNode.children.push(propertyTree);
      });

      entityType.navigationProperties.forEach(navigationProperty => {
        //å¤æ­æ¯å¦å¯¼èªå±æ§å¯¹åºçåç§°ä¸å¨ç¦å¿è¡¨ä¸­
        if (!(navigatorMap.get(navigationProperty.name + navigationProperty.navigationId))) {
          if (navigationProperty.type == NavigationType.EntitySet) {
            //è¥å¯¼èªå±æ§ç±»åä¸ºå®ä½é
            let entitySet = schema.entitySets.find(entitySet => entitySet.name == navigationProperty.name);
            let a = this.ConvertEntitySet(entitySet, schema, navigatorMap);
            a.parent = treeNode;
            treeNode.children.push(a);
          }//è¥ä¸ºå®ä½ç±»å
          else if (navigationProperty.type == NavigationType.EntityType) {
            let entityType = schema.entityTypes.find(entitytype => entitytype.id == navigationProperty.navigationId && entitytype.name == navigationProperty.name);
            let a = this.ConvertEntityType(entityType, schema, navigatorMap);
            a.parent = treeNode;
            treeNode.children.push(a);
          }
        }
        //è¥å¯¼èªå±æ§å¨ç¦å¿è¡¨ä¸­
        else {
          let navigatortree: TreeNode = {
            data:
            {
              "id": navigationProperty.navigationId,
              "name": navigationProperty.name,
              "type": "",
              "description": navigationProperty.description,
              "schema": this.currentSchemaName,
              "nodeType": "Complex"
            },
            children: [],
          };
          switch (navigatorMap.get(navigationProperty.name)) {
            case NavigationType.EntitySet:
              navigatortree.data.type = "EntitySet"; break;
            case NavigationType.EntityType:
              navigatortree.data.type = "EntityType"; break;
          }
          navigatortree.parent = treeNode;
          treeNode.children.push(navigatortree);
        }
      });
      return treeNode;
    }
    //è¥entitytypeä¸ºç©ºææªå®ä¹
    else return null;
  }

  private strContain(strr: any): boolean {
    let temp = strr + "";
    if (temp.indexOf("EntitySet") != -1)
      return true;
    else
      return false;
  }

  //===========åå²çº¿==========NewSchema===============

  /**
   * @description åå§åNewSchema
   */
  public onInitNewSchema() {
    //æ³¨å¥å®ä½å­å¨ä¸æå¼
    if (this.injectedEntities && this.injectedEntities.length > 0) {
      //åå§åï¼entities(é¡µé¢æ¾ç¤ºåå®¹);rootNode(è·èç¹);navigatorMap1(ç¦å¿åè¡¨)
      this.entities = [];
      let rootNode: TreeNode =
      {
        data:
        {
          "id": Math.random(),
          "name": "",
          "description": "",
          "type": "",
          "schema": "",
          "nodeType": ""
        },
        children: []
      };
      let navigatorMap1 = new Map<string, InjectedType>();

      //æ ¹æ®æ³¨å¥å®ä½çschemaNameæ¾å°å¯¹åºçrootSchema
      this.injectedEntities.forEach(injectEntity => {
        let rootSchema = this.newSchemaMap.get(injectEntity.schemaName);
        this.currentNewSchemaName = injectEntity.schemaName;
        //æ ¹æ®æ³¨å¥å®ä½ç±»å--->æ§è¡ä¸åæ¹æ³
        switch (injectEntity.injectedType) {
          case InjectedType.EntitySet: {
            let entitySet = rootSchema.entitySets.find(set => set.code == injectEntity.injectedName);
            rootNode = this.convertNewEntitySet(entitySet, rootSchema, navigatorMap1);
          }; break;
          case InjectedType.EntityType: {
            let entityType = rootSchema.entityTypes.find(type => type.code == injectEntity.injectedName);
            rootNode = this.convertNewEntityType(entityType, rootSchema, navigatorMap1);
          }; break;
        }
        rootNode.data.name = injectEntity.name;
        this.entities.push(rootNode);
      });
    }
  }

  /**
   * @description è½¬æ¢å®ä½ç±»å(EntityType)
   * @param entityType
   * @param schema
   * @param navigatorMap1
   * @returns
   */
  public convertNewEntityType(entityType: NewEntityType, schema: NewSchema, navigatorMap1: any): TreeNode {
    if (entityType) {
      //åå§åï¼NewEntityTypeçèç¹
      let treeNode: TreeNode =
      {
        data:
        {
          "id": entityType.code,
          "name": entityType.code,
          "description": entityType.name,
          "type": "EntityType",
          "schema": this.currentNewSchemaName,
          "nodeType": "Complex"
        },
        children: []
      };

      //pro
      navigatorMap1.set(entityType.name + entityType.code, InjectedType.EntityType);
      entityType.properties.forEach(pro => {
        let proNode: TreeNode =
        {
          data:
          {
            "id": Math.random(),
            "name": pro.code,
            "description": pro.name,
            "type": pro.dataType,
            "schema": this.currentNewSchemaName,
            "nodeType": "Simple"
          },
          children: []
        };
        proNode.parent = treeNode;
        treeNode.children.push(proNode);
      });

      //naPro
      entityType.navigationProperties.forEach(naPro => {
        //å¤æ­æ¯å¦å¯¼èªå±æ§å¯¹åºçåç§°å¨ç¦å¿è¡¨ä¸­
        if (!(navigatorMap1.get(naPro.navigationName + naPro.code))) {
          if (naPro.navigationType == NewNavigationType.EntityType) {
            let entityType = schema.entityTypes.find(type1 => type1.code == naPro.navigationName);
            let a = this.convertNewEntityType(entityType, schema, navigatorMap1);
            a.parent = treeNode;
            treeNode.children.push(a);
          } else if (naPro.navigationType == NewNavigationType.EntitySet) {
            let entitySet = schema.entitySets.find(set1 => set1.code == naPro.navigationName);
            let a = this.convertNewEntitySet(entitySet, schema, navigatorMap1);
            a.parent = treeNode;
            treeNode.children.push(a);
          }
        }
        //è¥å¯¼èªå±æ§å¨ç¦å¿è¡¨ä¸­
        else {
          let naProNode: TreeNode =
          {
            data:
            {
              "id": naPro.navigationName,
              "name": naPro.code,
              "description": naPro.name,
              "type": "",
              "schema": this.currentNewSchemaName,
              "nodeType": "Complex"
            },
            children: [],
          };
          switch (navigatorMap1.get(naPro.navigationName)) {
            case NewNavigationType.EntitySet:
              naProNode.data.type = "EntitySet"; break;
            case NewNavigationType.EntityType:
              naProNode.data.type = "EntityType"; break;
          }
          naProNode.parent = treeNode,
            treeNode.children.push(naProNode);
        }
      });
      return treeNode;
    }
    return null;
  }

  /**
   * @description è½¬æ¢å®ä½é(EntitySet)
   * @param set
   * @param schema
   * @param navigatorMap1
   * @returns
   */
  public convertNewEntitySet(set: NewEntitySet, schema: NewSchema, navigatorMap1: any): TreeNode {
    if (set) {
      //å°entitysetè½¬åä¸ºTreeNodeç»æç¨äºå±ç¤º
      let treeNode: TreeNode = {
        data:
        {
          "id": Math.random(),
          "name": set.code,
          "description": set.name,
          "type": "EntitySet" + "[" + set.code + "]",
          "schema": this.currentNewSchemaName,
          "nodeType": "Complex"
        },
        children: []
      };
      //å°è½¬åå®æçå®ä½éæ·»å è¿ç¦å¿è¡¨ï¼é²æ­¢å¾ªç¯è½¬å
      navigatorMap1.set(set.name, InjectedType.EntitySet);
      //æ¾å°å®ä½éå¯¹åºçå®ä½ç±»å
      let entityType = schema.entityTypes.find(entityType => entityType.code == set.entityType);
      //å°entitytypeè½¬åä¸ºTreeNodeç»æç¨äºå±ç¤º
      this.convertNewEntityType(entityType, schema, navigatorMap1).children.forEach(child => {
        treeNode.children.push(child);
      });
      return treeNode;
    }
    //å®ä½éä¸ºç©ºææªå®ä¹åè¿åç©º
    else return null;
  }

  //é¡¶çº§è¿æ»¤
  searchByFilter($event) {
    //å­å¨è¿æ»¤å¼&&treedataæå¼
    if ($event.value && $event.value != "" && this.treedata) {

      //childrenåç´ æ·±åº¦éå
      for (let i = 0; i < this.treedata.length; i++) {
        this.searchByFilter1($event.value, this.treedata[i]);
      }

      //è¿æ»¤undefined
      for (let i = 0; i < this.treedata.length; i++) {
        this.filter1(this.treedata[i]);
      }

    }
  }

  //childrenåç´ è¿æ»¤$event.value
  searchByFilter1(searchValue: string, node: any) {
    if (node.children && node.children.length > 0) {
      for (let i = 0; i < node.children.length; i++) {
        this.searchByFilter1(searchValue, node.children[i]);
      }
    } else if (node.children.length == 0) {
      if (node.data.description.indexOf(searchValue) == -1) {
        //indexOfè¿åæå®å­ç¬¦å¨å­ç¬¦ä¸²ä¸­ç¬¬ä¸æ¬¡åºç°çä½ç½®
        //-1ä»£è¡¨æ²¡æåºç°
        let temp = -1;
        if (node.parent != null) {
          for (let ii = 0; ii < node.parent.children.length; ii++) {
            if (node.parent.children[ii] && node.parent.children[ii].data.description == node.data.description) {
              temp = ii;
              delete node.parent.children[temp];
              break;
            }
          }
        }
      }
    }
  }

  //å»é¤undefindeåç´ 
  filter1(node: TreeNode) {
    if (node.children && node.children.length > 0) {
      var b = node.children.filter(item => item != undefined);
      node.children = b;
      for (let i = 0; i < node.children.length; i++) {
        if (node.children[i].children && node.children[i].children.length > 0) {
          this.filter1(node.children[i]);
        }
      }
    } else if (node.children.length == 0) {
    }
  }


  //è¿æ»¤
  filter($event) {
    //å¼å§è¿æ»¤
    // this.searchByFilter($event);
    // this.searchByFilter($event);

    this.tt.searchHandle.search('description', $event.value,  'client')

  }


}
