import { Pipe, PipeTransform, Inject, LOCALE_ID, Injectable } from '@angular/core';
import { FrameI18nResources } from './i18n/FrameI18nResource';

@Pipe({ name: 'locale' })
export class ExpressionEditorLocalePipe implements PipeTransform {
    private readonly defaultLang: string = "zh-CHS";
    private langCode: string;
    private lang: {};
    constructor(
        @Inject(LOCALE_ID) localeId: string
    ) {
        localeId = localStorage.getItem('languageCode');
        this.langCode = localeId || this.defaultLang;
        this.lang = FrameI18nResources[this.langCode] || FrameI18nResources[this.defaultLang];
    }
    transform(name: string): any {
        return this.lang[name] || name;
    }

}
