import { Component, OnInit, ElementRef, EventEmitter, Input, OnDestroy, Output, ViewChild } from '@angular/core';
import { forwardRef, Inject, NgZone } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { pick } from 'lodash-es';
import { MY_MONACO_EDITOR_CONFIG, MyMonacoEditorConfig } from './editorconfig';
import { CodeEditorEventService } from './my-monaco-editor-services/monaco-editor.event.service';
import { CODE_EDITOR_EVENTS } from './my-monaco-editor-services/events';
import { ExpressionEventService } from '../expression-event-service';

declare const monaco: any;

let loadedMonaco: boolean = false;
let loadPromise: Promise<void>;

// èªå®ä¹è¾å¥æ§ä»¶:1.å°è£ControlValueAccessor
// https://code-examples.net/zh-CN/q/2154761
export const CODE_EDITOR_INPUT_VALUE_ACCESSOR: any = {
  // https://blog.csdn.net/wangdan_2013/article/details/81314959
  provide: NG_VALUE_ACCESSOR,//
  useExisting: forwardRef(() => MyMonacoEditorComponent),//
  multi: true//
};
const monacoConfig: MyMonacoEditorConfig = {
  baseUrl: 'assets',
  defaultOptions: { scrollBeyondLastLine: false }
};
@Component({
  selector: 'app-my-monaco-editor',
  templateUrl: './my-monaco-editor.component.html',
  styleUrls: ['./my-monaco-editor.component.css'],
  // èªå®ä¹è¾å¥æ§ä»¶:2.å¼å¥ä¾èµæå¡ControlValueAccessor
  providers: [CODE_EDITOR_INPUT_VALUE_ACCESSOR,
    CodeEditorEventService,
    {
      provide: MY_MONACO_EDITOR_CONFIG,
      useValue: monacoConfig
    }
  ]
})

// èªå®ä¹è¾å¥æ§ä»¶ <-> Monaco Edtor

// èªå®ä¹è¾å¥æ§ä»¶:3.1 implements ControlValueAccessor 
export class MyMonacoEditorComponent implements ControlValueAccessor, OnDestroy, OnInit {
  protected _editor: any;
  private _options: any;
  // protected _windowResizeSubscription: Subscription;

  @ViewChild('codeEditor') _editorComponent: ElementRef; //å¨ææ·»å ä»£ç ç¼è¾å¨
  @Output() onInit;
  @Output() onChange;
  @Output() onTouched;

  @Input('options')
  set options(options: any) {
    // é»è®¤options(this.config.defaultOptions) + èªå®ä¹options(options)
    this._options = Object.assign({}, this.config.defaultOptions, options);
    if (this._editor) {
      this._editor.dispose();
      this.initMonaco(options);
    }
  }
  get options(): any {
    return this._options;
  }

  //å¨åæ å¤æå¥è¡¨è¾¾å¼çæ¹æ³
  insertWord(insertWord: string) {
    if (insertWord && insertWord != undefined && insertWord != "") {
      let p = {
        lineNumber: 1,
        column: 1
      };
      if (this._editor) {
        p = this._editor.getPosition();
      }
      let range = new monaco.Range(p.lineNumber,
        p.column,
        p.lineNumber,
        p.column);
      let id = { major: 1, minor: 1 };
      let text = insertWord;
      let op = { identifier: id, range: range, text: text, forceMoveMarkers: true };
      this._editor.executeEdits("my-source", [op]);
    }
  }

  // æ³¨å¥AngularMonacoEditorConfigï¼å¨åå»ºEditorå®ä¾æ¶è®¾ç½®config
  constructor(private zone: NgZone, @Inject(MY_MONACO_EDITOR_CONFIG) private config: MyMonacoEditorConfig, private codeEditorEventService: CodeEditorEventService, private eventService: ExpressionEventService) {

    // åå§åèªå®ä¹äºä»¶
    const self = this;
    codeEditorEventService.eventNames.forEach((name) => {
      // åå»ºèªå®ä¹äºä»¶ï¼æ­¤å¤ä½ç¨ç­æäº: @Output() onInit = new EventEmitter<any>() 
      self[name] = new EventEmitter<any>();
      const eventPair = pick(self, name);
      codeEditorEventService.addEvent(eventPair);
    });
  }

  ngOnInit(): void {
    if (loadedMonaco) {
      // Wait until monaco editor is available
      loadPromise.then(() => {
        this.initMonaco(this.options);
      });
    } else {
      loadedMonaco = true;
      loadPromise = new Promise<void>((resolve: any) => {
        // const baseUrl = this.config.baseUrl || '/assets';
        const baseUrl = '/platform/common/web/caf';
        // if (typeof((<any>window).monaco) === 'object') {
        //   resolve();
        //   return;
        // }
        const onGotAmdLoader: any = () => {
          // Load monaco
          (<any>window).require.config({ paths: { 'vs': `${baseUrl}/monaco/vs` } });
          (<any>window).require(['vs/editor/editor.main'], () => {
            this.onMonacoLoad();
            this.initMonaco(this.options);
            resolve();
          });
        };

        // Load AMD loader if necessary
        if (!(<any>window).require) {
          const loaderScript: HTMLScriptElement = document.createElement('script');
          loaderScript.type = 'text/javascript';
          loaderScript.src = `${baseUrl}/monaco/vs/loader.js`;
          loaderScript.onload = onGotAmdLoader;
          document.body.appendChild(loaderScript);
        } else {
          onGotAmdLoader();
        }
      });
    }
    //å¨åæ å¤æå¥è¡¨è¾¾å¼äºä»¶è®¢é
    let temp = this;
    this.eventService.currentExpression().subscribe((insertexpression: any) => {
      if (temp._editor != undefined) {
        temp.insertWord(insertexpression)
      }
    });
    this.eventService.clearExpression().subscribe(() => {
      if (temp._editor != undefined) {
        temp._editor.setValue("")
      }
    });
  }

  private onMonacoLoad(): void {
    const id = "foo.json";
    monaco.languages.json.jsonDefaults.setDiagnosticsOptions({
      validate: true,
      schemas: [{
        uri: "http://myserver/foo-schema.json",
        fileMatch: [id],
        schema: {
          type: "object",
          properties: {
            p1: {
              enum: ["v1", "v2"]
            },
            p2: {
              $ref: "http://myserver/bar-schema.json"
            }
          }
        }
      }, {
        uri: "http://myserver/bar-schema.json",
        fileMatch: [id],
        schema: {
          type: "object",
          properties: {
            q1: {
              enum: ["x1", "x2"]
            }
          }
        }
      }]
    })
  }

  ngOnDestroy() {
    // if (this._windowResizeSubscription) {
    //   this._windowResizeSubscription.unsubscribe();
    // }
    if (this._editor) {
      this._editor.dispose();
      this._editor = undefined;
    }
  }

  protected initMonaco(options: any): void {
    debugger;
    console.log("Init the custom monaco code editor.");

    // const hasModel = !!options.model;
    const hasModel = false;

    // if (hasModel) {
    //   options.model = monaco.editor.createModel(options.model.value, options.model.language, options.model.uri);
    // }

    this._editor = monaco.editor.create(this._editorComponent.nativeElement, options);

    if (!hasModel) {
      this._editor.setValue(this._value);
    }

    // monaco editor -> outside component
    this._editor.onDidChangeModelContent(() => this.onChangeModelContentHandler());

    this._editor.onDidBlurEditorText(() => this.onBlurEditorTextHandler());

    // refresh layout on resize event.
    // if (this._windowResizeSubscription) {
    //   this._windowResizeSubscription.unsubscribe();
    // }
    // this._windowResizeSubscription = fromEvent(window, 'resize').subscribe(() => this._editor.layout());

    this.codeEditorEventService.fireEvent({
      eventName: CODE_EDITOR_EVENTS.onInit,
      target: this,
      editor: this._editor
    });
  }

  onChangeModelContentHandler() {
    const _value = this._editor.getValue();

    // monaco editor -> outside component
    // https://github.com/JTangming/tm/issues/4 ngZoneè¯¦è§£
    this.zone.run(() => this.value = _value);// value is not propagated to parent when executing outside zone.
    // console.log("write from the monaco:" + this._value);
  }

  onBlurEditorTextHandler() {

    this.onTouchedHandler();
  }

  private _value: string = '';

  //get accessor
  get value(): any {
    return this._value;
  };

  //set accessor including call the onchange callback
  set value(v: any) {
    if (v !== this.value) {// æ³¨æè¿ç§åæ³ï¼å¼å¾å­¦ä¹ 
      this._value = v;
    }

    this.onChangeHandler(this.value);//å¨å±æ§ä¿®é¥°å¨éè°ç¨onchangeHandleræ¹æ³
  }

  localEditor() {//Demo: outside component -> monaco editor
    this.writeValue('test');
  }

  // èªå®ä¹è¾å¥æ§ä»¶:3.2 implements ControlValueAccesso

  // outside component -> monaco editor

  //From ControlValueAccessor interface
  writeValue(value: any) {
    this.value = value || '';

    // Fix for value change while dispose in process.
    setTimeout(() => {
      if (this._editor /*&& !this.options.model*/) {
        this._editor.setValue(this._value);
        // console.log("write to the editor:" + this._value);
      }
    });
  }

  //From ControlValueAccessor interface
  registerOnChange(fn: any) {
    this.onChangeHandler(this);
  }

  //From ControlValueAccessor interface
  registerOnTouched(fn: any) {
    this.onTouchedHandler();
  }

  //ControlValueAccessoræä¾çäºä»¶åè°
  onChangeHandler = (_: any) => { //Propagate Change to outside
    this.codeEditorEventService.fireEvent({
      eventName: CODE_EDITOR_EVENTS.onChange,
      target: this,
      data: _
    });
  };

  //ControlValueAccessoræä¾çäºä»¶åè°
  onTouchedHandler = () => {
    this.codeEditorEventService.fireEvent({
      eventName: CODE_EDITOR_EVENTS.onTouched,
      target: this
    });
  };
}