import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { Schema, EntityType, Property } from '../schema';
import { CurrentNode } from '../currentNode';

@Component({
  selector: 'app-expression-schema-info-grid',
  templateUrl: './expression-schema-info-grid.component.html',
  styleUrls: ['./expression-schema-info-grid.component.css']
})
export class ExpressionSchemaInfoGridComponent implements OnInit,OnChanges {
  
  @Input() schema = new Map<string, Schema>();
  @Input() choseSchemaTableName=new CurrentNode;
  @Output() selectSchemaRow = new EventEmitter();
  @Output() dbclickInsert = new EventEmitter();
  selectString:string="";
  items:Property[]=[];
  columns = [{ field: 'name', title: 'å±æ§åç§°',filter:true}];
  constructor() { }

  ngOnInit() {
  }
  ngOnChanges(): void {
    let schemaName = this.choseSchemaTableName.parentName;
    let tableId = this.choseSchemaTableName.id;
    let tableName = this.choseSchemaTableName.name;
    let schemaItem = this.schema.get(schemaName);
      if(schemaItem)
      {
        let entityType = schemaItem.entityTypes.find(entitytype => (entitytype.id==tableId && entitytype.name == tableName)||(entitytype.id=="" && entitytype.name == tableName));
        if(entityType)
        {
          this.items=entityType.properties;
          entityType.navigationProperties.forEach(item=>{
            let property = new Property;
            property.name = item.name;
            property.type = item.type.toString();
            property.description = item.description;
            this.items = this.items.filter(item=>item.name!==property.name);
            this.items.push(property);
          });
          this.selectString = entityType.name;
        }
        else
        {
          let entitySet = schemaItem.entitySets.find(entityset => entityset.name==tableName );
          if(entitySet)
          {
             let eType= schemaItem.entityTypes.find(entitytype => entitytype.name==entitySet.entityType);
             this.items = eType.properties;
             eType.navigationProperties.forEach(item=>{
              let property = new Property;
              property.name = item.name;
              property.type = item.type.toString();
              property.description = item.description;
              this.items = this.items.filter(item=>item.name!==property.name);
              this.items.push(property);
            })
            this.selectString = entitySet.name+".[0]";
          }
        }
      }
  }
  ondblclickRow($event)
  {
    this.dbclickInsert.emit(this.selectString+"."+$event.rowData.name);
  }
  selectRowChange($event)
  {
    this.selectSchemaRow.emit($event.data);
  }
}
