import { HttpClient } from '@angular/common/http';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { ExpressionEditorLocalePipe } from '../expression-editor-locale.pipe';
import { VarType } from './vartype';

@Component({
  selector: 'app-expression-var-grid',
  templateUrl: './expression-var-grid.component.html',
  styleUrls: ['./expression-var-grid.component.css']
})
export class ExpressionVarGridComponent implements OnInit {

  //å®ä¹i18næ è¯
  private localeId : string;
  private localePipe :  ExpressionEditorLocalePipe;
  
  constructor() { 
    //åå§åi18næ è¯
    this.localeId =  localStorage.getItem('languageCode');
    this.localePipe = new ExpressionEditorLocalePipe(this.localeId);
  }
  @Input() h1 : HttpClient;
  @Output() selectVarRow = new EventEmitter();
  columns = [
    { field: 'name', title: 'åç§°'},
    ];
  items=[];
  ngOnInit() { 
    this.items=[
      {
        name:this.localePipe.transform("VariableSessionValue"),
        id:VarType.Session
      },
      {
        name:this.localePipe.transform("VariableContextParameter"),
        id:VarType.Context
      }
    ]
  }
  selectRowChange($event)
  {
    this.selectVarRow.emit($event.data);
  }
}
