    private GetUnCamelParse(jsonObj: any) {
        for (let key in jsonObj) {
            if (typeof (jsonObj[key]) === "object") {
                this.GetUnCamelParse(jsonObj[key]);
            }
            let temp = jsonObj[key];
            delete (jsonObj[key]);
            jsonObj[this.ConvertToUncamel(key)] = temp;
        }
    }
    //å­ç¬¦ä¸²åè½¬æ¢Camel
    private ConvertToUncamel(input: string): string {
        if (input.length > 0) {
            let len = input.length;
            var strTemp = ""; //æ°å­ç¬¦ä¸²
            for (let i = 0; i < len; i++) {
                if (i == 0) {
                    strTemp += input[i].toLowerCase(); //ç¬¬ä¸ä¸ª
                }
                else {
                    strTemp += input[i];
                }
            }
            return strTemp;
        }
    }

