import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Schema, Property } from '../schema';
import { CurrentNode } from '../currentNode';
import { ExpressionEditorLocalePipe } from '../expression-editor-locale.pipe';
import { NewProperty, NewSchema } from '../i18n/NewSchema';

@Component({
  selector: 'app-expression-entity-info-grid',
  templateUrl: './expression-entity-info-grid.component.html',
  styleUrls: ['./expression-entity-info-grid.component.css']
})
export class ExpressionEntityInfoGridComponent implements OnInit {

  //å®ä¹i18næ è¯
  private localeId: string;
  private localePipe: ExpressionEditorLocalePipe;

  @Input() schema = new Map<string, Schema>();
  @Input() newSchemaMap = new Map<string, NewSchema>();   //ç»ä¸Schema
  @Input() choseEntityTableName = new CurrentNode;
  @Output() selectSchemaRow = new EventEmitter();
  @Output() dbclickInsert = new EventEmitter();
  selectString: string = "";
  items=[];
  columns = [];
  constructor() {
    //åå§åi18næ è¯
    this.localeId = localStorage.getItem('languageCode');
    this.localePipe = new ExpressionEditorLocalePipe(this.localeId);
  }

  ngOnInit() {
  }
  ngOnChanges(): void {
    // this.columns = [{ field: 'name', title: this.localePipe.transform("EntityPropList"), filter: true }];
    let schemaName = this.choseEntityTableName.schema;
    let parentName = this.choseEntityTableName.parentName;
    let tableId = this.choseEntityTableName.id;
    let tableName = this.choseEntityTableName.name;
    let schemaItem;
    if (this.newSchemaMap && this.newSchemaMap.size > 0) {
      //ç»ä¸Schema
      this.columns = [{ field: 'code', title: this.localePipe.transform("EntityPropList"), filter: true }];
      this.items = [];
      schemaItem = this.newSchemaMap.get(schemaName);
      if (schemaItem) {
        let entityType = schemaItem.entityTypes.find(entitytype => (entitytype.code == tableName ) || (entitytype.code == ""));
        if (!entityType) {
          let entitySet = schemaItem.entitySets.find(entityset => entityset.code == tableName);
          if (entitySet) {
            entityType = schemaItem.entityTypes.find(entitytype => entitytype.code == entitySet.entityType);
          }
        }
        this.items = [...entityType.properties];
        entityType.navigationProperties.forEach(item => {
          let property = new NewProperty;
          property.code = item.code;
          property.name = "";
          property.dataType = item.navigationType.toString();
          property.nullable = false;
          property.defaultValue = null;
          this.items.push(property);
          this.items = Array.from(this.items);
        });
        this.selectString = parentName + "." + entityType.name;
      }
    } else {
      //æ§çSchema
      this.columns = [{ field: 'name', title: this.localePipe.transform("EntityPropList"), filter: true }];
      this.items = [];
      schemaItem = this.schema.get(schemaName);
      if (schemaItem) {
        let entityType = schemaItem.entityTypes.find(entitytype => (entitytype.id == tableId && entitytype.name == tableName) || (entitytype.id == "" && entitytype.name == tableName));
        if (entityType) {
          this.items = [...entityType.properties];
          entityType.navigationProperties.forEach(item => {
            let property = new Property;
            property.name = item.name;
            property.type = item.type.toString();
            property.description = item.description;
            this.items.push(property);
          });
          this.selectString = parentName + "." + entityType.name;
        }
        else {
          let entitySet = schemaItem.entitySets.find(entityset => entityset.name == tableName);
          if (entitySet) {
            let eType = schemaItem.entityTypes.find(entitytype => entitytype.name == entitySet.entityType);
            this.items = [...eType.properties];
            eType.navigationProperties.forEach(item => {
              let property = new Property;
              property.name = item.name;
              property.type = item.type.toString();
              property.description = item.description;
              this.items.push(property);
            })
            this.selectString = parentName + "." + entitySet.name + ".[0]";
          }
        }
      }
    }
  }

  //æ°æ®ï¼å­ç»ä»¶åå»æå¥              (farrisåå»äºä»¶ï¼è°æ´)
  ondblclickRow($event) {
    //this.dbclickInsert.emit(this.selectString+"."+$event.rowData.name);
    if (this.newSchemaMap && this.newSchemaMap.size > 0) {
      this.dbclickInsert.emit($event.rowData.data.code);
    } else if (this.schema && this.schema.size > 0) {
      this.dbclickInsert.emit($event.rowData.data.name);
    }
  }
  selectRowChange($event) {
    this.selectSchemaRow.emit($event.data);
  }
}
